@font-face {
  font-family: "aasheim";
  src: url("https://jontore.github.io/aasheim/aasheim.woff");
}

body {
  font-family: 'futura';
}


html {
  --main-color: rgb(220,13,59); 
  --secondary-color:  rgb(5,77,147);
  --third-color: rgb(254,184, 75);
}

h1, h2, h3, h4,
.app--title
{
  font-family: "aasheim";
  color: black;
  text-decoration: normal;
}
  .App {
  text-align: center;
}

a {
  text-decoration: unset;
  color: black;
}

ul, li  {
  display: inline-block;
  list-style-type: none;
  margin: 0;
}

.itemlist {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
}

.nav {
  font-size: 2rem;
}

.nav ul {
  margin: 1rem;
  padding: 0;
  display: flex;
}

.nav--item + .nav--item {
  margin-left: 2rem;
}

.nav--right {
  margin-left: auto;
}

.item {
}

.item + .item {
  margin-left: 1em;
}

.item--image {
  width: 20vw;
}

.button {
  display: block;
  margin: auto;
  font-family: 'aasheim';
  font-size: 1.2rem;
  width: 100%;
  max-width: 20rem;
  padding: 0.5rem;
  color: white;
  background-color: var(--third-color);
  border: 0;
}

.button:hover {
  cursor: pointer;;
}

.button__full {
  width: 100%;
}

.item:nth-child(3n+1) .button {
  background-color: var(--secondary-color);
}

.item:nth-child(3n+2) .button {
  background-color: var(--main-color);
}

.checkout {
  width: 60vw;
  margin: 0 20vw;
}

.checkout--table {
  width: 100%;
  margin: auto;
  font-size: 1.2rem;
  text-align: left;
}

.checkout--cell + .checkout--cell {
  padding-left: 1rem;
}

.checkout--total {
  display: flex;
  justify-content: flex-end;  
}

.checkout--cell--item {
  display: inline-block;
  vertical-align: middle;
}

.checkout--cell--button {
  background: transparent;
  border-top: none;
  border-left: none;
  border-right: none;
  border-bottom: 0.1rem solid var(--third-color);
}

.checkout--cell--button:hover {
  border-bottom: 0.1rem solid black;
}

.checkout--cell--button:focus {
  outline: none;
  border-bottom: 0.1rem solid var(--secondary-color);
}

.checkout--input {
  font-size: 1.3rem;
}

.checkout--input input {
  margin-left: 1rem;
  border: none;
  border-bottom: 0.2rem solid black;
  font-size: 1.2rem;
}

.checkout--input input:focus {
  outline: none;
  border-bottom: 0.2rem solid var(--secondary-color);
  font-size: 1.3rem;
}

.checkout--error {
  color: var(--main-color);
  font-size: 1rem;
}

.error-container {
  background-color: var(--main-color);
  opacity: 0.3;
  color: white;
  padding: 0.2rem;
  margin-bottom: 2rem;
}

.error-container h3 {
  color: white;
  font-size: 1.1rem;
}

@media screen and (max-width: 600px) {
  .button {
    width: 100%;
  }
}